<template>
  <div class="container">
      <div class="quiz">
          
          <div class="quiz--header">
              <h1 v-if="winner==''">Pytanie numer {{numberOfQuestion+1}}</h1>
              <h1 v-else-if="winner=='SANGWINIK'">{{jsonFile.types[0].type}}</h1>
              <h1 v-else-if="winner=='CHOLERYK'">{{jsonFile.types[1].type}}</h1>
              <h1 v-else-if="winner=='MELANCHOLIK'">{{jsonFile.types[2].type}}</h1>
              <h1 v-else-if="winner=='FLEGMATYK'">{{jsonFile.types[3].type}}</h1>
              </div>

          <div class="quiz--main">
            <div v-if="winner==''">
              <h1>{{jsonFile.questions[numberOfQuestion].question}}</h1>
              <ul>
                  <li><button @click="Sangwinik()">
                    {{jsonFile.questions[numberOfQuestion].answers[0]}}</button></li>
                  <li><button @click="Choleryk()">
                    {{jsonFile.questions[numberOfQuestion].answers[1]}}</button></li>
                  <li><button @click="Melancholik()">
                    {{jsonFile.questions[numberOfQuestion].answers[2]}}</button></li>
                  <li><button @click="Flegmatyk()">
                    {{jsonFile.questions[numberOfQuestion].answers[3]}}</button></li>
              </ul>
             

            </div>
            <div class="result">
            <h2 v-if="winner=='SANGWINIK'">{{jsonFile.types[0].desc}}</h2>
            <h2 v-else-if="winner=='CHOLERYK'">{{jsonFile.types[1].desc}}</h2>
            <h2 v-else-if="winner=='MELANCHOLIK'">{{jsonFile.types[2].desc}}</h2>
            <h2 v-else-if="winner=='FLEGMATYK'">{{jsonFile.types[3].desc}}</h2>
            </div>


          </div>
      </div>
  </div>
</template>

<script>
import quizFile from './assets/quiz.json';

export default {
  name: 'Quiz',
  data() {
    return {
      numberOfQuestion: 0,
      SANGWINIK: 0,
      CHOLERYK: 0,
      MELANCHOLIK: 0,
      FLEGMATYK: 0,
      jsonFile: quizFile,
      winner: '',
    };
  },
  methods: {
    maxQuestions() {
      if (this.numberOfQuestion >= 20) {
        if (this.SANGWINIK >= this.CHOLERYK && this.SANGWINIK >= this.MELANCHOLIK && this.SANGWINIK >= this.FLEGMATYK) {
          this.winner = 'SANGWINIK';
        } else if (this.CHOLERYK >= this.SANGWINIK && this.CHOLERYK >= this.MELANCHOLIK && this.CHOLERYK >= this.FLEGMATYK) {
          this.winner = 'CHOLERYK';
        } else if (this.MELANCHOLIK >= this.SANGWINIK && this.MELANCHOLIK >= this.CHOLERYK && this.MELANCHOLIK >= this.FLEGMATYK) {
          this.winner = 'MELANCHOLIK';
        } else if (this.FLEGMATYK >= this.SANGWINIK && this.FLEGMATYK >= this.CHOLERYK && this.FLEGMATYK >= this.MELANCHOLIK) {
          this.winner = 'FLEGMATYK';
        }
      }
    },
    nextQuestion() {
      this.numberOfQuestion += 1;
      this.maxQuestions();
    },
    Sangwinik() {
      this.SANGWINIK += 1;
      this.nextQuestion();
    },
    Choleryk() {
      this.CHOLERYK += 1;
      this.nextQuestion();
    },
    Melancholik() {
      this.MELANCHOLIK += 1;
      this.nextQuestion();
    },
    Flegmatyk() {
      this.FLEGMATYK += 1;
      this.nextQuestion();
    },
  },
};
</script>


